.main {
    width: 100%;
    display: flex;
    flex-direction: column;
}
.header {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}
.header h4 {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #37395C;
    text-transform: uppercase;
}
.header a {
    text-transform: uppercase;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #37395C;
    text-decoration: none;
}
.header a:hover {
    text-decoration: underline;
}
.slider {
    width: 100%;
    margin-top: 30px;
}

@media screen and (max-width: 568px) {
    .item {
        width: 95% !important;
        height: auto;
    }
    .item img {
        height: auto !important;
        object-fit: cover;
        aspect-ratio: 1 / 1;
    }
}

@media screen and (max-width: 468px) {
    .item {
        width: 100% !important;
        /* padding: 16px !important; */
        box-sizing: border-box;
    }
}